import React , {useState,useEffect} from "react";
import './MultiDatePicker.css'

const MultiDatePicker = ({color , format}) => {
   const [startselectedDate,setstartSelectedDate] =useState(null);
   const [endselectedDate,setendSelectedDate] =useState(null);

   const handlestartDateChange = (e) =>{
    setstartSelectedDate(e.target.value)
   }

   const dateCompare = () =>{
    console.log("startselectedDate",startselectedDate)
    console.log("endselectedDate",endselectedDate)

    const startDateParts = startselectedDate.split("-");
    const endDateParts = endselectedDate.split("-");
    

    const startYear = parseInt(startDateParts[0], 10);
    const startMonth = parseInt(startDateParts[1], 10) - 1;
    const startDay = parseInt(startDateParts[2], 10);
  
    const endYear = parseInt(endDateParts[0], 10);
    const endMonth = parseInt(endDateParts[1], 10) - 1;
    const endDay = parseInt(endDateParts[2], 10);
  
    const startDateObj = new Date(startYear, startMonth, startDay);
    const endDateObj = new Date(endYear, endMonth, endDay);
    console.log("startDateObj",startDateObj)

   }

   useEffect(() => {
    // This code will be executed after selectedDate has been updated
    console.log('Date has been updated:', endselectedDate);
  }, [endselectedDate]);

   const handleendDateChange = (e) =>{
    setendSelectedDate(e.target.value)
   }

   const style = {
    backgroundColor: color,
   }

   return(
    <div className="container">
        <div className="left-div" style={style}>
        <input
        className="datepicker-input"
        type="date"
        value= {startselectedDate}
        onChange={handlestartDateChange}
        placeholder={format}
        />
    </div>
    <div className="right-div" style={style}>
        <input
        className="datepicker-input"
        type="date"
        value= {endselectedDate}
        onChange={handleendDateChange}
        placeholder={format}
        />
    </div>
    </div>
    
   )

}
export default MultiDatePicker;